.App {
  text-align: center;
  margin: 0;
  padding: 0;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.block{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
}
.sad_message{
  width: 35%;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-content: flex-start;
  font-size: 18px;
}
.hello_h{
  font-weight: bold;
  margin-bottom: 1em;
}
.hello{
  width: 100%;
}